.actions-toggle {

    .actions-toggle-trigger {

        @include media($small) {
            display: none;
        }

   }

    .actions-content {
        display: none;
        clear: both;

        @include media($small) {
            display: block;
            padding: 0;
        }

        &.active {
            display: block;
        }

        > .custom-fieldset {
           display: block;
        }

        > a,
        > button,
        .custom-fieldset.init {
            display: inline-block;
            vertical-align: top;
            margin: $sm-spacing $tiny-spacing $sm-spacing 0;

            &:last-child {
                margin-right: 0;
            }

        }
   }

}

.actions-content {
    padding: $sm-spacing 0;

    @include media($large) {
        display: block;
        border: none;
    }

    .dropdown-menu {
        @extend .list-reset;
    }

    .button-secondary.dropdown {

        &.open {
            border-bottom: 0;
            border-bottom-left-radius: 0;
            border-bottom-right-radius: 0;
        }

    }

    .custom-select {
        margin-bottom: 0;
    }

}


.actions-inline {

    button,
    .button,
    .custom-fieldset {
        display: inline-block;
        @include lrswap(margin, $sm-spacing $sm-spacing $sm-spacing 0);
    }

    &.actions-right {
        text-align: right;

        button,
        .button,
        .custom-fieldset {
            @include lrswap(margin, $sm-spacing 0 $sm-spacing $sm-spacing);
        }

    }

}

.actions-stacked + .actions-content {

    li {
        @include float(left);
        @include lrswap(padding, 0 $sm-spacing $sm-spacing 0);

        @include media($large) {
            float: none;
            @include lrswap(padding, 0 0 $sm-spacing 0);
        }

        &:last-child {
            padding: 0;
        }

    }

}
