.fixed-bottom {

    .button-primary {
        @include float(right);
        margin: 0 $sm-spacing;

        @include media($small) {
            float: none;
            margin: 0;
        }
    }

    .form-field {

        @include media($mobile-down) {

            label,
            .input-label {
                margin-top: $sm-spacing;
            }

            .custom-select {
                margin-bottom: $sm-spacing;
            }
        }
    }
}
